import React from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';

const antaList = {
  0 : {A : 'あんた！', B : 'よ！あんた！！'},
  1 : {A : 'あんたほんと...', B : 'ってあんた！'},
  2 : {A : 'あんたあんたまじでさぁ', B : 'ってあんたさぁ！'},
  3 : {A : 'あんた', B : 'あんた'},
  4 : {A : 'あんたたち...', B : 'はやめときなさいよあんた'},
  5 : {A : 'あんたオタクほんと', B : 'なんだからあんた！'},
  6 : {A : 'あんた！！！！！！！！', B : 'ってちょっとあんた！！！！！！'},
  7 : {A : 'あんた！？', B : 'ってちょっとあんた！？！！？！'},
  8 : {A : 'ほんとあんたさぁ...' , B : 'はないわよあんた...'},
  9 : {A: 'お？喧嘩すっか？', B: 'discoあがれよ！W'}
}

const BodyStyle = styled.div`
  text-align: center;
`;

const InputStyle = styled.div`
  border: solid 1px;
  // margin-left: 100%;
  // margin-right: 100%;
  margin-top: 1%;
  padding: 1px;
`;

class InputForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      antaText: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {

    const min = 0;
    const max = 9;
    const num = Math.floor( Math.random() * (max + 1 - min) ) + min ;

    if(num !== 9){
      this.setState({
        antaText : antaList[num].A + antaList[num].B,
      })
    }else{
      this.setState({
        antaText : antaList[num].A + this.state.value + antaList[num].B,
      });
    }
    event.preventDefault();
  }


  render() {
    return (
      <BodyStyle>
        {/* タイトル */}
        <div>
          <h1>あんたコンバータ</h1>
          <p>小花衣が好きすぎる</p>
        </div>

        {/* 入力 */}
        <InputStyle>
          <form onSubmit={this.handleSubmit}>
            <label>
              あんたなに！？　＞＞　 
              <input type="text" value={this.state.value} onChange={this.handleChange} />
            </label>
            <input type="submit" value="あんた変換" />
          </form>
        </InputStyle>
        
        {/* 返却 */}
        <div>
          <h3>
          {this.state.antaText !== '' && (
            <p>？？？「{this.state.antaText}」</p>
          )}
          </h3>
        </div>
      </BodyStyle>
    );
  }
}

// ========================================

ReactDOM.render(
  <InputForm />,
  document.getElementById('root')
);


